<template>
  <div v-if="job && job.id">
    <h1>{{ job.title }}</h1>
    <h2>{{ job.description }}</h2>
    <h3>{{ job.id }}</h3>
  </div>
  <div v-else><p>Trying to load...</p></div>
</template>

<script>
export default {
    props: ['id'],
    data(){
      return{
        job: null
      }
    },
    mounted(){
      fetch('https://my-json-server.typicode.com/RiskyWilhelm/vuejs-router-test-5/availablejobs/' + this.id)
        .then(res => res.json())
        .then(data => this.job = data)
        .then(err => console.log(err.message))
    }
}
</script>

