<template>
  <h1>u want money?</h1>
  <div v-if="jobs.length">
    <div v-for="job in jobs" :key="job.id" class="job">
        <router-link :to="{ name: 'jobdetails', params: { id: job.id } }">
            <h2>{{job.title}}</h2>
        </router-link>
    </div>
  </div>
  <div v-else><p>Trying to load...</p></div>
</template>

<script>
export default {
    data(){
        return{
            jobs: []
        }
    },
    mounted(){
        fetch('https://my-json-server.typicode.com/RiskyWilhelm/vuejs-router-test-5/availablejobs')
            .then(res => res.json())
            .then(data => this.jobs = data)
            .then(err => console.log(err.message))
    }
}
</script>

<style>
    .job h2{
        max-width: 600px;
        padding: 20px;
        margin: 10px auto;
        border-radius: 20px;
        background-color: rgb(51, 59, 66);
        color: rgb(230, 230, 230);
    }
    .job h2:hover{
        background-color: rgb(119, 64, 51);
    }
    .job a{
        text-decoration: none;
    }
</style>